.myBtn {
	border: none;
	padding: 5px;
	background: none;
	color: white;
	transition: all 0.4s ease;
	font-size: 20px;
	z-index: 200;

}

.primary:hover {
	color: rgba(255, 102, 0, 0.4);
	border-radius: 15px;
	transition: all 0.4s ease;
}


.active {
	color: rgb(255, 102, 0);
	cursor: default;
}