.wrapper-home {
	display: flex;
	justify-content: center;
	align-items: center;
	background: url('https://imprimis.hillsdale.edu/wp-content/uploads/1991/02/Filming-of-a-Movie.jpg');
}

.home {
	// border: 3px solid rgb(255, 102, 0);
	padding: 30px;
	text-align: center;
	background: rgba(78, 75, 75, 0.699);
	border-radius: 40px;

	&-card {
		margin-right: 20px;
		text-align: center;
		cursor: pointer;
		transition: 0.35s ease transform;

		&:hover {
			transform: scale(1.05);
			transition: 0.35s ease transform;
		}

		&__item {
			margin-bottom: 10px;

			&:last-child {
				margin-bottom: 0;
			}
		}

		&:last-child {
			margin-right: 0;
		}
	}
}

.description {
	margin-top: 100px;
}