body {
	background: rgb(58, 55, 55);
	color: white;
	font-family: Roboto;
}

.App {
	max-width: 2048px;
	margin: 0 auto;
	height: 100vh;

	.appWrapper {
		min-height: 100%;
		display: flex;
		flex-direction: column;
	}

	.wrapper {

		background-attachment: fixed;
		padding: 20px;
		flex: 1 1 auto;

	}

	.background {
		background: rgba(78, 75, 75, 0.699);

	}

	.title {
		text-transform: capitalize;
		margin-left: 100px;
		margin-bottom: 20px;
		font-size: 24px;
	}
}