.loader-wrapper {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	backdrop-filter: blur(10px);
	z-index: 100;



	.loader {
		width: 100px;
		height: 100px;
		border-radius: 50%;
		border: 3px dashed rgb(255, 102, 0);
		animation: rotate 0.5s infinite linear;
	}
}

.local {
	position: absolute;
}




@keyframes rotate {
	from {
		transform: rotate(0deg) scale(1);
	}

	to {
		transform: rotate(360deg) scale(1.1);
	}
}