.menu {
	cursor: pointer;
	display: none;
	color: white;

	@media (max-width: 735px) {
		display: block;
	}

	&-content {
		position: absolute;
		top: 50px;
		left: -300px;
		background: rgba(32, 30, 30, 0.801);
		z-index: 100;
		transition: all 0.4s ease;
		display: flex;
		flex-direction: column;

		&__active {
			position: absolute;
			top: 0px;
			left: 0px;
			padding: 70px 20px 20px 20px;
			width: 100%;
			background: rgba(32, 30, 30, 0.801);
			z-index: 100;
			transition: all 0.4s ease;
			display: flex;
			flex-direction: column;
		}
	}

	.burger {
		height: 20px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		// margin-right: 10px;
		transition: 0.4s ease transform;
		position: relative;


		&__active {
			height: 20px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			margin-right: 10px;
			transform: rotate(90deg);
			transition: 0.4s ease transform;
			position: relative;
			z-index: 200;
		}

		&-line {
			margin-bottom: 5px;
			background: white;
			width: 20px;
			height: 2px;


			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}