.navBar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 20px 40px;
	font-size: 26px;
	background-color: rgb(78, 75, 75);

	.leftBar {
		display: flex;
		align-items: center;

		&-buttons {
			@media (max-width: 735px) {
				display: none;
			}
		}

		.logo {
			margin-right: 40px;
			display: flex;
			align-items: center;

		}
	}

}